import React, { Component } from 'react'
import './rooms.scss'
const friends = [{ name: 'Julien', status: 'online' },
  { name: 'Not Julien', status: 'away' },
  { name: 'Maybe Julien', status: 'offline' },
  { name: 'Likely Julien', status: 'offline' }]

export class Rooms extends Component {
  render () {
    return (
      <div className='rooms-container'>
        <div className='rooms-window'>
          <h1>Find a Study Room</h1>
          <hr/>
          <div className='friends'>
            <h2>Friends:</h2>
            <ul>
              {friends.map((friend, index) => 
                <li key={index}>
                  <h3>{friend.name}</h3>
                  <p>{friend.status}</p>
                </li>
              )}
            </ul>
          </div>
          <hr/>
          <div className='actions'>
            <a href="/tasks/3564">Get Matched</a>
            <a href="/tasks/153">Create a Room</a>
          </div>
        </div>
      </div>
    )
  }
}

export default Rooms
